@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}

.templateCart {
	.page-header {
		margin-bottom: 31px;
		.page-heading {
			font-size: 35px;
			font-weight: 500;
		}
		.cart-count {
			color: var(--primary-color);
			padding: 0 7px;
		}
		&.text-center {
			padding: 40px 0;
			margin-bottom: 0;
			>svg {
				margin-bottom: 19px;
			}
			.page-heading .text {
				margin-bottom: 20px;
				display: block;
				font-size: 36px;
				font-weight: 600;
				line-height: 1.45em;
			}
			.btn {
				min-width: 215px;
			}
		}
	}
	.cart-table-header {
		display: none;
	}
	.cart-table-body {
		.cart-item {
			margin-bottom: 20px;
			padding-bottom: 20px;
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
	.cart-item {
		position: relative;
	}
	.item-inner {
		.item-media, cart-remove-button {
			display: none;
		}
	}
	cart-remove-button {
		svg {
			width: 10px;
			height: 10px;
		}
		.loading {
			.icon-loading {
				display: block;
			}
			.icon-remove {
				display: none;
			}
		}
	}
	.item-mobile-image {
		flex: 0 0 90px;
		max-width: 90px;
		@include margin-end(15px);
		cart-remove-button {
			@include ab(0,n,n,0);
			z-index: 1;
			a {
				padding: 10px 10.5px;
				display: flex;
				background: var(--miscellaneous-color);
				border-radius: var(--border-radius);
				svg {
					fill: #fff;
				}
			}
		}
	}
	.discounts__discount {
		display: flex;
		align-items: center;
		margin-top: 5px;
		font-size: 13px;
		>.icon {
			display: flex;
			@include margin-end(8px);
		}
	}
	.item-line-price {
		display: none;
	}
	.item-has-sale {
		.price {
			color: var(--price-sale-color);
		}
	}
	.cart-item__error {
		display: none;
		align-items: center;
		margin: 10px 0;
	}
	.item-total-price {
		margin-top: 10px;
	}
	.item-has-sale, .item-has-discounted {
		price-item {
			display: block;
		}
	}
	.cart-item__quantity-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		cart-remove-button {
			@include padding-start(5px);
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 30px;
				height: 30px;
				@media (min-width: 750px) and (max-width: 800px) {
					width: auto;
				}
			}
			.loading {
				.icon-loading {
					display: block;
					width: 15px;
					height: 15px;
				}
				.icon-remove {
					display: none;
				}
			}
		}
	}
	.cart-item__quantity {
		order: 5;
	}
	.quantity-select {
		background: #F0F0F0;
		max-width: 110px;
		display: flex;
		position: relative;
		border-radius: var(--border-radius);
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			appearance: none;
			margin: 0;
		}
		input {
			width: calc(100% - 72px);
			padding: 0;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			font-size: 15px;
			height: 32px;
			border: 0;
		}
		button {
			padding: 10px;
			width: 36px;
			height: 32px;
			display: flex;
			align-items: center;
			justify-content: center;
			.icon-minus {
				width: 7px;
				height: 7px;
			}
		}
		.icon-loading {
			display: none;
			width: 15px;
			height: 15px;
			@include ab(50%,50%,n,n);
			@include margin-start(-7.5px);
			margin-top: -7.5px;
		}
		&.loading {
			.icon-loading {
				display: block;
			}
			input {
				opacity: 0;
				visibility: hidden;
			}
		}
	}
	.item-detail {
		margin: 0 0 10px;
		width: 100%;
		@include padding-end(32px);
		a {
			color: var(--text-color);
			font-size: inherit;
		}
	}
	.cart-table {
		margin-bottom: 30px;
	}
	.btn-clear-cart {
		min-width: 165px;
		text-align: center;
	}
	.btn-wrapper {
		margin-bottom: 50px;
	}
	@media (min-width: 750px) {
		.cart-table-header {
			display: flex;
			border-bottom: 1px solid var(--miscellaneous-color);
		}
		.table-header {
			padding: 2px 0 13px;
			font-weight: 700;
			font-size: 14px;
		}
		.table-col-price {
			flex: 0 0 150px;
			max-width: 150px;
			text-align: center;
		}
		.table-col-quantity {
			flex: 0 0 150px;
			max-width: 150px;
			text-align: center;
		}
		.table-col-subtotal {
			flex: 0 0 150px;
			max-width: 150;
			text-align: center;
		}
		.item-detail {
			@include padding-end(0);
		}
		.item-inner {
			display: flex;
			width: 100%;
			.item-media {
				display: block;
			}
			>div {
				padding-top: 20px;
			}
			cart-remove-button {
				flex: 0 0 32px;
				display: flex;
				a {
					display: flex;
					align-items: center;
					height: 100%;
					width: 100%;
					transition: 0.2s all ease-in-out;
				}
				&:hover {
					a {
						opacity: 0.67;
					}
				}
			}
		}
		.item-header {
			display: flex;
			align-items: center;
			flex: 1;
			.item-media {
				flex: 0 0 90px;
				max-width: 90px;
				@include margin-end(15px);
			}
		}
		.item-line-price {
			flex: 0 0 150px;
			max-width: 150px;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: center;
			.price {
				margin: 0;
				font-size: calc(var(--body-fs-xl) + 1px);
			}
		}
		.item-qty {
			flex: 0 0 150px;
			max-width: 150px;
			display: flex;
			align-items: center;
			justify-content: space-between;
			order: unset;
		}
		.quantity-select {
			max-width: 126px;
			button {
				height: 46px;
				width: 43px;
			}
			input {
				height: 46px;
			}
		}
		.cart-item__quantity-wrapper {
			justify-content: center;
		}
		.item-total-price {
			flex: 0 0 150px;
			max-width: 150px;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 0;
			.price {
				font-size: calc(var(--body-fs-xl) + 1px);
			}
		}
	}
	@media (min-width: 1300px) {
		.cart-items-section {
			flex: 1;
		}
		.cart-wrapper {
			display: flex;
			margin: 0 -10px;
			>section {
				padding: 0 10px;
			}
		}
	}
}